import { useState, useEffect, useCallback, useRef } from 'react';
import { marketingBot } from '@/lib/marketingBot';

interface MarketingStats {
  products: number;
  campaigns: number;
  transactions: number;
  affiliates: number;
  revenue: number;
}

const initialStats: MarketingStats = {
  products: 0,
  campaigns: 0,
  transactions: 0,
  affiliates: 0,
  revenue: 0
};

export function useMarketingData() {
  const [isLoading, setIsLoading] = useState(false);
  const [isConnected, setIsConnected] = useState(false);
  const [stats, setStats] = useState<MarketingStats>(initialStats);
  const [error, setError] = useState<string | null>(null);
  const [isLoadingStats, setIsLoadingStats] = useState(false);
  const retryCountRef = useRef(0);

  const fetchWithTimeout = async (promise: Promise<any>, timeout: number = 5000) => {
    const timeoutPromise = new Promise((_, reject) => {
      setTimeout(() => reject(new Error('Request timed out')), timeout);
    });
    return Promise.race([promise, timeoutPromise]);
  };

  const connectBot = useCallback(async () => {
    setIsLoading(true);
    setError(null);
    try {
      const success = await marketingBot.login();
      setIsConnected(success);
      if (!success) {
        setError('Failed to connect to the bot');
      }
    } catch (error) {
      console.error('Failed to connect bot:', error);
      setError('Connection error occurred');
      setIsConnected(false);
    } finally {
      setIsLoading(false);
    }
  }, []);

  const fetchStats = useCallback(async () => {
    if (!isConnected) return;

    setIsLoadingStats(true);
    setError(null);

    try {
      const [products, campaigns, transactions, affiliates] = await Promise.allSettled([
        fetchWithTimeout(marketingBot.getProducts()),
        fetchWithTimeout(marketingBot.getCampaigns()),
        fetchWithTimeout(marketingBot.getTransactions()),
        fetchWithTimeout(marketingBot.getAffiliateStats())
      ]);

      const results = {
        products: products.status === 'fulfilled' ? products.value : [],
        campaigns: campaigns.status === 'fulfilled' ? campaigns.value : [],
        transactions: transactions.status === 'fulfilled' ? transactions.value : [],
        affiliates: affiliates.status === 'fulfilled' ? affiliates.value : { totalAffiliates: 0 }
      };

      // Calculate total revenue safely
      const revenue = results.transactions.reduce((sum: number, t: any) => {
        const amount = parseFloat(t.amount);
        return sum + (isNaN(amount) ? 0 : amount);
      }, 0);

      // Update stats with the new data
      setStats({
        products: results.products.length || 0,
        campaigns: results.campaigns.length || 0,
        transactions: results.transactions.length || 0,
        affiliates: results.affiliates?.totalAffiliates || 0,
        revenue
      });

      // Handle any failed requests
      const failedRequests = [products, campaigns, transactions, affiliates]
        .filter(result => result.status === 'rejected')
        .map(result => (result as PromiseRejectedResult).reason);

      if (failedRequests.length > 0) {
        const timeoutErrors = failedRequests.filter(error => error.message === 'Request timed out').length;
        const networkErrors = failedRequests.filter(error => error.message.includes('network')).length;
        const otherErrors = failedRequests.length - timeoutErrors - networkErrors;

        const errorParts = [];
        if (timeoutErrors > 0) {
          errorParts.push(`${timeoutErrors} request${timeoutErrors > 1 ? 's' : ''} timed out`);
        }
        if (networkErrors > 0) {
          errorParts.push(`${networkErrors} network error${networkErrors > 1 ? 's' : ''}`);
        }
        if (otherErrors > 0) {
          errorParts.push(`${otherErrors} other error${otherErrors > 1 ? 's' : ''}`);
        }

        const errorMessage = `Some data may be incomplete (${errorParts.join(', ')}).`;
        setError(errorMessage);

        // Implement smart retry logic
        if (retryCountRef.current < 3) {
          const delay = Math.min(1000 * Math.pow(2, retryCountRef.current), 10000); // Max 10s delay
          setTimeout(() => {
            retryCountRef.current++;
            fetchStats();
          }, delay);
        } else {
          // After max retries, try one final time after a longer delay
          setTimeout(() => {
            retryCountRef.current = 0; // Reset retry count
            fetchStats();
          }, 30000); // Wait 30s before final retry
        }
      } else {
        retryCountRef.current = 0; // Reset retry count on full success
        setError(null); // Clear any previous errors
      }
    } catch (error) {
      console.error('Failed to fetch stats:', error);
      setError('Failed to fetch latest statistics. Retrying...');
      
      // Implement exponential backoff for complete failures
      if (retryCountRef.current < 5) {
        const delay = Math.min(1000 * Math.pow(2, retryCountRef.current), 30000);
        setTimeout(() => {
          retryCountRef.current++;
          fetchStats();
        }, delay);
      }
    } finally {
      setIsLoadingStats(false);
    }
  }, [isConnected]);

  useEffect(() => {
    let mounted = true;
    let reconnectAttempts = 0;
    let reconnectTimeout: NodeJS.Timeout;

    const attemptReconnect = async () => {
      if (!mounted || isConnected) return;
      
      try {
        reconnectAttempts++;
        await connectBot();
        
        // If still not connected and under max attempts, try again with exponential backoff
        if (!isConnected && mounted && reconnectAttempts < 5) {
          const delay = Math.min(1000 * Math.pow(2, reconnectAttempts), 30000); // Max 30s delay
          reconnectTimeout = setTimeout(attemptReconnect, delay);
        }
      } catch (error) {
        console.error('Reconnection attempt failed:', error);
        // Still retry on error if under max attempts
        if (mounted && reconnectAttempts < 5) {
          const delay = Math.min(1000 * Math.pow(2, reconnectAttempts), 30000);
          reconnectTimeout = setTimeout(attemptReconnect, delay);
        }
      }
    };

    // Start connection attempt immediately
    attemptReconnect();

    // Also attempt reconnect when connection is lost
    const connectionCheckInterval = setInterval(() => {
      if (!isConnected && mounted) {
        reconnectAttempts = 0; // Reset attempts when starting a new reconnection cycle
        attemptReconnect();
      }
    }, 60000); // Check connection every minute

    return () => {
      mounted = false;
      if (reconnectTimeout) {
        clearTimeout(reconnectTimeout);
      }
      clearInterval(connectionCheckInterval);
    };
  }, [isConnected, connectBot]);

  useEffect(() => {
    let mounted = true;
    let updateInterval: NodeJS.Timeout;

    const updateStats = async () => {
      if (mounted && isConnected) {
        await fetchStats();
      }
    };

    if (isConnected) {
      // Reset retry count when connection state changes
      retryCountRef.current = 0;
      updateStats();
      updateInterval = setInterval(updateStats, 30000);
    } else {
      // Reset stats and error state when disconnected
      setStats(initialStats);
      setError(null);
      // Also reset retry count when disconnected
      retryCountRef.current = 0;
    }

    return () => {
      mounted = false;
      if (updateInterval) {
        clearInterval(updateInterval);
      }
    };
  }, [isConnected, fetchStats]);

  return {
    isLoading,
    isConnected,
    stats,
    error,
    isLoadingStats,
    connectBot,
    fetchStats
  };
}
