import axios from 'axios';

interface Product {
  id?: string;
  name: string;
  description: string;
  price: number;
}

interface Campaign {
  id?: string;
  name: string;
  budget: number;
  status: 'active' | 'paused' | 'completed';
}

export class MarketingBot {
  private email: string;
  private password: string;
  private token: string | null = null;
  private retryCount = 3;
  private retryDelay = 1000;

  constructor() {
    this.email = process.env.NEXT_PUBLIC_EXTERNAL_EMAIL || '';
    this.password = process.env.NEXT_PUBLIC_EXTERNAL_PASSWORD || '';
  }

  private async retryOperation<T>(operation: () => Promise<T>, fallback: T): Promise<T> {
    let lastError: any;
    for (let i = 0; i < this.retryCount; i++) {
      try {
        return await operation();
      } catch (error) {
        lastError = error;
        if (i < this.retryCount - 1) {
          await new Promise(resolve => setTimeout(resolve, this.retryDelay));
        }
      }
    }
    console.error('Operation failed after retries:', lastError);
    return fallback;
  }

  // Authentication
  async login(): Promise<boolean> {
    return this.retryOperation(async () => {
      const response = await axios.post('/api/auth/login', {
        email: this.email,
        password: this.password
      });
      this.token = response.data.token;
      return true;
    }, false);
  }

  // Product Management
  async createProduct(product: Product): Promise<Product | null> {
    return this.retryOperation(async () => {
      const response = await axios.post('/api/products', product, {
        headers: { Authorization: `Bearer ${this.token}` }
      });
      return response.data;
    }, null);
  }

  async getProducts(): Promise<Product[]> {
    return this.retryOperation(async () => {
      const response = await axios.get('/api/products', {
        headers: { Authorization: `Bearer ${this.token}` }
      });
      return response.data;
    }, []);
  }

  // Campaign Management
  async createCampaign(campaign: Campaign): Promise<Campaign | null> {
    return this.retryOperation(async () => {
      const response = await axios.post('/api/campaigns', campaign, {
        headers: { Authorization: `Bearer ${this.token}` }
      });
      return response.data;
    }, null);
  }

  async getCampaigns(): Promise<Campaign[]> {
    return this.retryOperation(async () => {
      const response = await axios.get('/api/campaigns', {
        headers: { Authorization: `Bearer ${this.token}` }
      });
      return response.data;
    }, []);
  }

  // Payment Processing
  async getTransactions(): Promise<any[]> {
    return this.retryOperation(async () => {
      const response = await axios.get('/api/transactions', {
        headers: { Authorization: `Bearer ${this.token}` }
      });
      return response.data;
    }, []);
  }

  // Affiliate Management
  async getAffiliateStats(): Promise<any> {
    return this.retryOperation(async () => {
      const response = await axios.get('/api/affiliates/stats', {
        headers: { Authorization: `Bearer ${this.token}` }
      });
      return response.data;
    }, { totalAffiliates: 0 });
  }

  // AI Product Research
  async researchProducts(query: string): Promise<any[]> {
    try {
      const response = await axios.post('/api/ai/research', { query }, {
        headers: { Authorization: `Bearer ${this.token}` }
      });
      return response.data;
    } catch (error) {
      console.error('Failed to research products:', error);
      return [];
    }
  }

  // Analytics
  async getAnalytics(): Promise<any> {
    try {
      const response = await axios.get('/api/analytics', {
        headers: { Authorization: `Bearer ${this.token}` }
      });
      return response.data;
    } catch (error) {
      console.error('Failed to fetch analytics:', error);
      return null;
    }
  }
}

export const marketingBot = new MarketingBot();
